import request from "@/js/request";
import { setCancelSource } from "@/js/cancelToken";

/**
 * 管理员登录
 * method: 'post','get'
 * @param {*} params.account  必填 账号
 * @param {*} params.password  必填  密码
 */
export function loginInApi(params) {
  const data = {
    username: params.account,
    password: params.password
  };
  return request({
    url: "/customer/v1/auth/login",
    method: "post",
    data,
    showloading: true,
    cancelToken: setCancelSource()
  });
}


/**
 * 非密码登录
 * method: 'post','get'
 * @param {*} params.account  必填 账号
 * @param {*} params.role  必填  角色 1/0
 */
export function noPassLoginInApi(params) {
  const data = {
    account: params.account,
    role: params.role
  };
  return request({
    url: "/chat/customerSignIn",
    method: "post",
    data,
    showloading: true,
    cancelToken: setCancelSource()
  });
}

// 注册
export function registerApi(params) {
  const data = {
    account: params.account,
    nickName: params.nickName,
    password: params.password,
    role:params.role,
    realName:params.realName,
    email:params.email,
  };
  return request({
    url: "/chat/addUser",
    method: "post",
    data,
    showloading: true,
    cancelToken: setCancelSource()
  });
}

// 退出
export function loginOutApi() {
  return request({
    url: "/chat/loginOut",
    method: "post",
    showloading: true,
    cancelToken: setCancelSource()
  });
}



