<template>
  <div class="fullscreen">
    <section class="screen_header">
      <a-layout-header>
        <div class="logo">
          <img src="../../assets/image/logo.png" alt>
        </div>
        <div class="right">
          <div  :class="curreyRouterName=='applist'? 'activeLine activeLine1':''"></div>
          <div :class="curreyRouterName=='personInfor'? 'activeLine activeLine2':''"></div>

          <ul>
            <li>
              <span @click="gotoApp" :class="curreyRouterName=='applist'? 'activeTopMenu':''">
                <i class="iconfont amd-icon_managment"></i>
              </span>
            </li>
            <li>
              <div class="changeLang">
                <lang class="switchLang" :showIcon="false"></lang>
              </div>
            </li>
            <li>
              <a-dropdown class="userMenu" :trigger="['click']">
                <a class="ant-dropdown-link" href="#">
                  <a-avatar :size="30" :src="avatar"/>
                  <a-icon type="down"/>
                </a>
                <a-menu slot="overlay">
                  <a-menu-item>
                    <a href="javascript:;" @click="goToUser">个人信息</a>
                  </a-menu-item>
                  <a-menu-item>
                    <a href="javascript:;" @click="loginOut">{{$t('login.loginOut')}}</a>
                  </a-menu-item>
                </a-menu>
              </a-dropdown>
            </li>
          </ul>
        </div>
      </a-layout-header>
    </section>
    <section class="screen_con">
      <subLeftMenu :collapsed="collapsed"></subLeftMenu>
      <a-layout-content>
        <router-view></router-view>
      </a-layout-content>
    </section>
  </div>
</template>
<script>
import Cookies from "@/js/cookies";
import lang from "@/components/change-lang";
import subLeftMenu from "@/components/subLeftMenu";
export default {
  name: "mainLagout",
  data() {
    return {
      newRoute: [Cookies.get("menu")],
      openKeys: ["sub1"],
      collapsed: false,
      avatar: Cookies.get("avatar"),
    };
  },
  components: {
    lang,
    subLeftMenu
  },
  computed: {
    curreyRouterName() {
      return this.$route.name;
    },
  },
  inject: ["reload"],
  methods: {
    loginOut() {
      Cookies.remove("TokenKey");
      Cookies.remove("TokenKey_refresh");
      this.$router.push("/");
    },
    gotoApp() {
      Cookies.set("menu", "applist");
      this.reload();   //reload 可以去掉导航菜单选中状态的class
      this.$router.push({ name: "applist" });
    },
    goToUser() {
      Cookies.set("menu", "personInfor");
      this.reload();   //reload 可以去掉导航菜单选中状态的class
      this.$router.push({ name: "personInfor" });
    }
  }
};
</script>
<style lang="less">
@import "../../css/main.less";
</style>




