<template>
    <a-dropdown>
      <!-- <a class="ant-dropdown-link" href="#">
      Hover me <a-icon type="down" /> -->
      <span v-if="showIcon"> {{$t('common.switchLang')}}</span>
      <!-- <span v-else><img src="../assets/image/svg/menu_language.svg" class="globalSvg"></span> -->
      <span v-else><i class="iconfont amd-icon_language"></i></span>
      
    <a-menu slot="overlay" @click="switchLang">
      <a-menu-item key="en-US">EngLish</a-menu-item>
      <a-menu-item key="id-ID">Bahasa</a-menu-item>
      <a-menu-item key="zh-CN">中文</a-menu-item>
    </a-menu>
    <!-- <a-button class="switchButton">
       <span v-if="showIcon"> {{$t('common.switchLang')}}</span>
      <span v-else><img src="../assets/image/svg/menu_language.svg" class="globalSvg"></span>
     
      <i class="iconfont icon-caretdown"></i>
    </a-button> -->
  </a-dropdown>
</template>

<script>
import Cookies from "@/js/cookies";

//切换语言组件
export default {
  name: "change-lang",
  props: {
    showIcon: false //页面显示类型(icon/文字)
  },
  data() {
    return {
      lang: ""
    };
  },
  inject: ["reload"], //注入reload 刷新方法
  methods: {
    switchLang(name) {
      this.$i18n.locale = name.key;
      Cookies.set("play_lang", name.key, { expires: 1 });
      this.$store.dispatch("switchLang", name.key); // 如果你要自己实现多语言切换，那么只需要执行这行代码即可，修改语言类型
      this.reload();
    }
  },
  created() {
    this.lang = Cookies.get("play_lang") || "zh-CN";
  }
};
</script>
<style lang="less">
  // .switchButton{
  //   border: none;
  //   background: none;
  //   span{
      
  //   }
  // }
  img{
        width: 30px;
      }
</style>




