import axios from "axios";
import { getToken, setToken } from "@/js/auth";
import { router } from "@/router/index";
import Vue from "vue";
import { message } from "ant-design-vue";
import { showFullScreenLoading, tryHideFullScreenLoading } from "./loading";
import store from "@/store";

var vue = new Vue();
// create an axios instance
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 15000, // request timeout
  "Content-Type": "multipart/form-data",
  "Access-Control-Allow-Origin": "*",
  "Access-Control-Allow-Headers": "X-Requested-With,Content-Type",
  "Access-Control-Allow-Methods": "PUT,POST,GET,DELETE,OPTIONS"
});
var isRefreshing = false;
// refeshToken的时候保存正在进行的请求  refeshToken后接着请求
let refreshRequests = [];
/*将所有的请求都push到数组中*/
function pushsubscribeTokenRefresh(cb) {
  console.log(cb);
  refreshRequests.push(cb);
}
// 数组中的请求得到新的token之后自执行，用新的token去请求数据
function onRrefreshed(token) {
  refreshRequests.map(cb => cb(token));
}

function configToken(config) {
  console.log("config" + JSON.stringify(config));
  if (config.tokenType && config.tokenType == 1) {
    console.log("token1");
    if (getToken("TokenKey")) {
      config.headers["Authorization"] = "Bearer " + getToken("TokenKey");
    }
  } else if (config.tokenType && config.tokenType == 2) {
    console.log("token2");
    if (getToken("TokenKey_2")) {
      config.headers["Authorization"] = "Bearer " + getToken("TokenKey_2");
    }
  }
}
// request interceptor
service.interceptors.request.use(
  config => {
    console.log(config);
    // alert("token1:" + getToken("TokenKey"));
    // alert("token2:" + getToken("TokenKey_2"));
    // alert("TokenKey_refresh:" + getToken("TokenKey_refresh"));
    //TokenKey_refresh 2个半小时过期，过期后跳回到登录界面
    if (getToken("TokenKey_refresh")) {
      //TokenKey/TokenKey_2 2个小时过期，过期后刷新TokenKey_refresh值
      if (!getToken("TokenKey") || !getToken("TokenKey_2")) {
        config.headers["Authorization"] =
          "Bearer " + getToken("TokenKey_refresh");
        if (!isRefreshing) {
          // /*判断是否正在刷新*/
          // console.log(vue.$store);
          isRefreshing = true;
          store.dispatch("refreshToken").then(
            res => {
              console.log("refresh成功", res);
              isRefreshing = false;
              setToken("TokenKey", res.data.token, 110);
              console.log("新的token", getToken("TokenKey"));
              setToken("TokenKey_2", res.data.token_2, 110);
              setToken("TokenKey_refresh", res.data.refresh_token, 140);
              onRrefreshed(res.data.token);
              /*执行onRefreshed函数后清空数组中保存的请求*/
              refreshRequests = [];
            },
            err => {
              console.log(err); //refresh失败跳转到登录
            }
          );
          /*把请求(token)=>{....}都push到一个数组中*/
          let retry = new Promise((resolve, reject) => {
            /*(token) => {...}这个函数就是回调函数*/
            pushsubscribeTokenRefresh(token => {
              console.log("pushsubscribeTokenRefresh token", token);
              config.headers["Authorization"] = "Bearer " + token;
              resolve(config);
            });
          });
          console.log("return retry", retry);
          return retry;
        }
        console.log("return config1");
        if (config.showloading != false) {
          showFullScreenLoading();
        }
        return config;
      } else {
        configToken(config);
      }
    }
    if (config.showloading != false) {
      showFullScreenLoading();
    }
    console.log("return config2");
    return config;
  },
  error => {
    return Promise.reject(error);
  }
);

// respone interceptor
service.interceptors.response.use(
  response => {
    console.log("response", response);
    if (response.status == 200) {
      if (response.data.code == 200) {
        tryHideFullScreenLoading();
        return Promise.resolve(response.data);
      } else if (
        response.data.code == 400) {
        tryHideFullScreenLoading();
        return Promise.reject(response.data);
      }else {
        return Promise.reject(response);
      }
    } else {
      return Promise.reject(response);
    }
  },
  error => {
    httpErrorStr(error);
    return Promise.reject(error);
  }
);


function httpErrorStr(error) {
  tryHideFullScreenLoading();
  if (error.toString().indexOf("Network Error") != -1) {//系统返回 无code 网络错误
    alertMessage("networkError"); //networkError
  } else if (error.toString().indexOf("500") != -1) {
    alertMessage("系统错误"); //系统错误
  } else if (error.toString().indexOf("401") != -1 && error.response.data.code == 400000) {
    alertMessage(vue.$t('error.forcedlogoff')); //账户在其他地方登录，您被迫下线
    // removeToken();
  } else if (error.message == "stopQuest") {
    return false;
  } else {
    alertMessage("请求失败"); //请求失败
  }
}
//弹出提示语
function alertMessage(msg) {
  message.config({
    top: `40px`,
    duration: 3,
    maxCount: 1,
  });
  message.error(msg)
}

export default service;



