<template>
  <!-- 左菜单 -->
  <a-layout-sider collapsible :collapsed="collapsedNow">
    <a-button @click="toggleCollapsed" class="menucollapsed"><span :class="collapsedNow==false?'bigMenuIcon':'smallMenuIcon'"><span>|</span><span class="middleSpan">|</span><span>|</span></span></a-button>
    <a-menu mode="inline" :defaultSelectedKeys="newRoute" :defaultOpenKeys="defaultOpenKeys" :openKeys="openKeys" @openChange="onOpenChange" @click="changeMenu" :inlineCollapsed="collapsed">
      <a-sub-menu key="identityCheck">
        <span slot="title">
          <i class="iconfont amd-icon_identity"></i>
          <span>{{$t('main.identityCheck')}}</span>
        </span>
        <a-menu-item key="companyInfo">{{$t('main.companyInformation')}}</a-menu-item>
        <a-menu-item key="checkPersonInfo">{{$t('main.personalInformation')}}</a-menu-item>
        <a-menu-item key="npwpInfo">{{$t('main.npwpInformation')}}</a-menu-item>
      </a-sub-menu>

      <a-sub-menu key="faceRes">
        <span slot="title">
          <i class="iconfont amd-icon_face"></i>
          <span>{{$t('main.faceRecognize')}}</span>
        </span>
        <a-menu-item key="faceComparisonRequest">{{$t('main.faceComparison')}}</a-menu-item>
        <a-menu-item key="idHoldingRequest">{{$t('main.idHoldingTitle')}}</a-menu-item>
        <a-menu-item key="findFaceRequest">Find More Face</a-menu-item>
      </a-sub-menu>

      <a-menu-item key="documentMatchRequest">
        <i class="iconfont amd-icon_documentmatch"></i>
        <span>{{$t('main.documentMatch')}}</span>
      </a-menu-item>
      <a-sub-menu key="documentExtraction">
        <span slot="title">
          <i class="iconfont amd-icon_documentextrac"></i>
          <span>{{$t('main.documentExtraction')}}</span>
        </span>
        <a-menu-item key="signatureRequest">{{$t('main.signature')}}</a-menu-item>
        <a-menu-item key="fingerprintRequest">{{$t('main.fingerprint')}}</a-menu-item>
        <a-menu-item key="photographRequest">{{$t('main.photograph')}}</a-menu-item>
      </a-sub-menu>
      <a-menu-item key="idCard">
        <i class="iconfont amd-icon_ocr"></i>
        <span>{{$t('main.ocr')}}</span>
      </a-menu-item>
    </a-menu>
  </a-layout-sider>
</template>
<script>
import Cookies from "@/js/cookies";
export default {
  name: "backSubmenu",
  data() {
    return {
      newRoute: [Cookies.get("menu")],
      collapsedNow: false,
      rootSubmenuKeys: ['identityCheck', 'faceRes', 'documentExtraction'],  
      openKeys:[Cookies.get("openMenu")],         //点击打开的父亲菜单
      defaultOpenKeys:[Cookies.get("openMenu")],   //父菜单的key  刷新和收缩时使用 来保存之前打开的父菜单
    };
  },
  props: {
    collapsed: Boolean
  },
  methods: {
    changeMenu(item) {
      this.$router.push({ name: item.key });
      Cookies.set("menu", item.key);
      console.log(this.openKeys);
      if(this.openKeys[0]){    //没有子菜单的 没有openKey
        Cookies.set("openMenu",this.openKeys[0]);  //刷新 自动打开当前submenu
        this.defaultOpenKeys=this.openKeys;
      }
    },
    toggleCollapsed() {
      console.log(this.collapsedNow);
      this.collapsedNow = !this.collapsedNow;
      if(this.collapsedNow){   //收缩时关闭打开的submenu
        this.openKeys = [];
      }else{                          //打开时 打开当前的submenu
        this.openKeys[0] =this.defaultOpenKeys[0];
      }
    },
    onOpenChange (openKeys) {
      const latestOpenKey = openKeys.find(key => this.openKeys.indexOf(key) === -1);
      if (this.rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
        this.openKeys = openKeys
      } else {
        this.openKeys = latestOpenKey ? [latestOpenKey] : []
      }
    },
  },
  mounted() {
    this.collapsedNow = this.collapsed;
  },
  created(){
    console.log(this.newRoute);
  }
};
</script>
<style lang="less">
@import "../css/main.less";
</style>



