import request from "@/js/request";
import { setCancelSource } from "@/js/cancelToken";


export function refreshTokenApi() {
  return request({
    url: "customer/v1/auth/refresh-token",
    method: "post",
    showloading: true,
    tokenType: 1,
    // cancelToken: setCancelSource()
  });
}

//获取applist
export function getApplistApi(valInfo) {
  const data = {
    page: valInfo.page
  }
  return request({
    url: "customer/v1/app/get-app-list",
    method: "post",
    data: data,
    showloading: valInfo.showFullLoading == true ? true : false,
    tokenType: 1,
    // cancelToken: setCancelSource()
  });
}
//app 详情
export function getAppDetailApi(valInfo) {
  // console.log(val);
  const data = {
    app_id: valInfo.app_id,
    app_key: valInfo.app_key
  }
  console.log(data);
  return request({
    url: "customer/v1/app/get-app-info",
    method: "post",
    data: data,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//添加app
export function addAppApi(valInfo) {
  console.log('接受数据',valInfo);
  // const data = {
  //   icon:valInfo.avatar ,
  //   name:valInfo.name,
  //   callback:valInfo.callback ,
  //   message_check_token:valInfo.message_check_token ,
  //   message_encryption:valInfo.message_encryption ,
  //   white_list:valInfo.white_list ,
  //   tag: valInfo.tag,
  //   description: valInfo.description,
  //   website:valInfo.website,
  // }

  let formdata = new FormData();
  formdata.append("icon", valInfo.avatar);
  formdata.append("name", valInfo.name);
  formdata.append("callback", valInfo.callback);
  formdata.append("message_check_token", valInfo.message_check_token);
  formdata.append("message_encryption", valInfo.message_encryption);
  formdata.append("white_list", valInfo.white_list);
  formdata.append("tag", valInfo.tag);
  formdata.append("description", valInfo.description);
  formdata.append("website", valInfo.website);
  // console.log(data);
  return request({
    url: "customer/v1/app/add-app",
    method: "post",
    data: formdata,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//获取app密钥
export function getAppSecretApi(valInfo) {
  let data = {
    app_id: valInfo.app_id,
    app_key: valInfo.app_key,
  }
  console.log(data);
  return request({
    url: "customer/v1/app/generate-app-secret",
    method: "post",
    data: data,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//修改app
export function editAppApi(valInfo) {
  
  let formdata = new FormData();
  formdata.append("icon", valInfo.avatar);
  formdata.append("callback", valInfo.callback);
  formdata.append("message_check_token", valInfo.message_check_token);
  formdata.append("message_encryption", valInfo.message_encryption);
  formdata.append("white_list", valInfo.white_list);
  formdata.append("tag", valInfo.tag);
  formdata.append("description", valInfo.description);
  formdata.append("website", valInfo.website);
  formdata.append("app_id", valInfo.app_id);
  formdata.append("app_key", valInfo.app_key);

  // console.log(formdata);
  return request({
    url: "customer/v1/app/edit-app-info",
    method: "post",
    data: formdata,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//禁用启用app
export function changAppStatusApi(valInfo) {
  let data={
    app_id:valInfo.app_id,
    app_key:valInfo.app_key,
    status:valInfo.status
  }
  return request({
    url: "customer/v1/app/change-app-status",
    method: "post",
    data: data,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//获取用户信息
export function getUserInfoApi() {
  return request({
    url: "customer/v1/user-info/get-user-info",
    method: "post",
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//修改密码
export function editPasswordApi(valInfo) {
  let data = {
    old_password: valInfo.old_password,
    new_password: valInfo.new_password,
    new_password_repeat:valInfo.new_password
  }
  console.log(data);
  return request({
    url: "customer/v1/user-info/change-password",
    method: "post",
    data: data,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}
//修改用户信息
export function changeUserInfoApi(valInfo) {
  console.log(valInfo);
  let formdata = new FormData();
  formdata.append("icon", valInfo);
  return request({
    url: "customer/v1/user-info/change-userinfo",
    method: "post",
    data: formdata,
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}


