import Vue from "vue";
import VueRouter from "vue-router";
import { routers } from "./router";
import Cookies from "@/js/cookies";

Vue.use(VueRouter);

// 路由配置
const RouterConfig = {
  //   mode: 'history',
  mode: "hash",
  routes: routers
};

export const router = new VueRouter(RouterConfig);

// 路由拦截
// 需要鉴权
const whiteList = ["/login", "/register", "/service", "/user"]; //免登录白名单

router.beforeEach((to, from, next) => {
  if (Cookies.get("TokenKey_refresh")) {
    //判断是否登录
    if (to.path == "/login") {
      next({ name: "companyInfo" });
    } else {
      next();
    }
  } else {
    //未登录  进入登录页面
    if (whiteList.indexOf(to.path) !== -1) {
      next();
    } else {
      next({ name: "login" });
    }
  }
});
router.afterEach(to => {
  window.scrollTo(0, 0);
});



