import request from "@/js/request";
import { setCancelSource } from "@/js/cancelToken";

//举着照片
export function holdingIdApi(params) {
  return request({
    url: "v1/face_recognition/recognition/holding_card",
    method: "post",
    data:params,
    showloading: true,
    cancelToken: setCancelSource()
  });
}

//两张照片对比
export function twoFaceApi(params) {
  return request({
    url: "v1/face_recognition/recognition/face_noun",
    method: "post",
    data:params,
    showloading: true,
    cancelToken: setCancelSource()
  });
}

//身份证ocr识别
export function OcrApi(params) {
  return request({
    url: "v1/image/classify/tfclassifier",
    method: "post",
    data:params,
    showloading: true,
    cancelToken: setCancelSource()
  });
}


