import request from "@/js/request";
import { setCancelSource } from "@/js/cancelToken";
//公司检测
export function companyCheckApi(params) {
    console.log(params);
    let data={
      companyName:params.companyName,
      companyZipCode:params.companyZipCode
    }
    return request({
      url: "app/v1/company-check/check",
      method: "post",
      data:data,
      showloading: true,
      tokenType:2,
      cancelToken: setCancelSource()
    });
}

// 个人检测
export function personCheckApi(params) {
  console.log(params);
  let data={
    name:params.name,
    idNumber:params.idNumber,
  }
  return request({
    url: "app/v1/identity-check/check",
    method: "post",
    data:data,
    showloading: true,
    tokenType:2,
    cancelToken: setCancelSource()
  });
}

//npwp税务检测
export function npwpCheckApi(params) {
  console.log(params);
  let data={
    name:params.name,
    npwp:params.npwp
  }
  return request({
    url: "app/v1/npwp-check/check",
    method: "post",
    data:data,
    showloading: true,
    tokenType:2,
    cancelToken: setCancelSource()
  });
}


