import request from "@/js/request";
import { setCancelSource } from "@/js/cancelToken";

export function getApiListApi() {
  return request({
    url: "customer/v1/api/get-api-list",
    method: "post",
    data: {},
    showloading: true,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

export function getLogApi(params) {
  let data = {
    serviceApiId: params.serviceApiId,
    page: params.page,
    pageSize: params.pageSize
  };
  return request({
    url: "customer/v1/log/get-log",
    method: "post",
    data: data,
    // showloading: true,
    showloading: params.showFullLoading,
    tokenType: 1,
    cancelToken: setCancelSource()
  });
}

//两张图片对比
export function faceNounApi(params) {
  let formdata = new FormData();
  formdata.append("image_file_1", params.image_file_1);
  formdata.append("image_file_2", params.image_file_2);
  return request({
    url: "app/v1/face/recognition/face-noun",
    method: "post",
    data: formdata,
    showloading: true,
    tokenType: 2,
    cancelToken: setCancelSource()
  });
}

